
#banner-bg {
  position: fixed;
  z-index: 2;
  top: 0;
  left: 0;
  width: 100%;
  height: 250px;
  background: url(../static/banner.png) center;
  background-size: cover;
}
#main {
  margin-top: 250px;
  display: -moz-box; 
  display: -webkit-box; 
  display: box; 
  width: 100%;
  height: 100%;
}
#nav {
  position: fixed;
  height: 100%;
  width: 200px;
  background-color: #bcdcbf;
  margin-top: -250px;
  padding-top: 250px;
  z-index: 1;
}
#timeline {
  margin-left: 220px;
  position: relative;
}
.mobile-icon {
  display: none;
  position: fixed;
  margin-top: -250px;
  z-index: 5;
  width: 100px;
  height: 100px;
}
.mobile-nav-div {
  position: fixed;
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto; 
  z-index: 4;
  margin-top: -250px;
}
.mobile-nav {
  position: fixed;
  padding: 40px;
  padding-top: 200px;
  display: block;
  position: relative; 
  -webkit-box-flex: 1; 
  box-flex: 1;
  background-color: #bcdcbf;
  height: 100%;
  width: 400px;
}
.mobile-nav ul {
  margin: 0;
  padding: 0;
  text-align: center;
}
.mobile-nav li {
  margin: 0 5px 10px 0;
  padding: 40px;
  list-style: none;
  font-size: 2.5em;
}
.mobile-nav a {
  padding: 3px 12px;
  text-decoration: none;
  color: #999;
  line-height: 100%;
}
.mobile-nav a:hover {
  color: #000;
}
.sidebar-enter-active, .sidebar-leave-active {
  -webkit-transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.sidebar-enter, .sidebar-leave-active {
  -webkit-transform: translateX(-200px); 
  transform: translateX(-200px);
}
.fade-enter-active {
  -webkit-transition: opacity .5s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  transition: opacity .5s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.fade-enter, .fade-leave-active {
  opacity: 0
}
@media screen and (min-device-width: 600px) {
.mobile-nav-div {
    -webkit-transition: all .1s cubic-bezier(1.0, 0.5, 0.8, 1.0);
    transition: all .1s cubic-bezier(1.0, 0.5, 0.8, 1.0);
    -webkit-transform: translateX(-210px);
    transform: translateX(-210px);
}
}
@media screen and (max-device-width: 600px) {
.mobile-icon {
    display: block;
}
.mobile-nav-div {
    -webkit-transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
    transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
#nav {
    display: none;
}
#timeline {
    margin-left: 20px;
}
}



