
.nav {
	position: relative;
	margin: 20px 0;
	padding-top: 50px;
}
.nav ul {
	margin: 0;
	padding: 0;
	text-align: center;
}
.nav li {
	margin: 0 5px 10px 0;
	padding: 10px;
	list-style: none;
}
.nav a {
	padding: 3px 12px;
	text-decoration: none;
	color: #999;
	line-height: 100%;
}
.nav a:hover {
	color: #000;
}



